export const loadState = () => {
  try {
    const serializedState = localStorage.getItem('favoritedGifs');

    if(serializedState === null) {
      return undefined;
    }

    return JSON.parse(serializedState);

  } catch(err) {
    return undefined;
  }
}

export const saveState = (state) => {
  try {
    const serializedState = JSON.stringify(state);
    localStorage.setItem('favoritedGifs', serializedState);
  } catch (err) {
    // Ignore write errors.
  }
};



